import d3 from "d3";
import React from "react";
import Radium from "radium";

@Radium
class VictoryPie extends React.Component {
  constructor(props) {
    super(props);
    this.color = d3.scale.ordinal()
      .range(["#98abc5", "#8a89a6", "#7b6888", "#6b486b", "#a05d56", "#d0743c", "#ff8c00"]);
  }

  drawSlices(slices) {
    const radius = Math.min(this.props.width, this.props.height) / 2;

    const pie = d3.layout.pie()
      .sort(null)
      .value(function (d) { return d.population; });

    const arcData = pie(slices);

    const arc = d3.svg.arc()
        .outerRadius(radius - 10)
        .innerRadius(radius - 70);

    const sliceComponents = slices.map((slice, index) => {
      return this.props.slice({
        index,
        slice,
        arc,
        fill: this.color(slice.age),
        arcData: arcData[index]
      });
    });

    return (
      <g>
        {sliceComponents}
      </g>
    );
  }

  render() {
    return (
      <g transform={"translate(" + this.props.width / 2 + "," + this.props.height / 2 + ")"}>
        {this.drawSlices(this.props.data)}
      </g>
    );
  }
}

VictoryPie.propTypes = {
  height: React.PropTypes.number,
  width: React.PropTypes.number,
  slice: React.PropTypes.func,
  data: React.PropTypes.arr
};

export default VictoryPie;



